import {  all, fork } from 'redux-saga/effects'

import { getBusTimetableSaga } from './timetable.sagas'
import { getWeatherSaga } from './weather.sagas'

export default function* root() {
  yield all([
    fork(getBusTimetableSaga),
    fork(getWeatherSaga)
  ])
}



