import { delay } from 'redux-saga';
import { put, takeLatest, call, select } from 'redux-saga/effects'
import fetch from 'isomorphic-fetch'

import * as actions from '../actions'

export function fetchBusTimetable(busStop) {
  return fetch(`https://cors-anywhere.herokuapp.com/https://www.metlink.org.nz/api/v1/StopDepartures/${busStop}`)
    .then(response => response.json() )
}

export function* getBusTimetable() {
  const busStop = yield select(state => state.induction.busStop)
  const timetable = yield call(fetchBusTimetable, busStop)
  const services = []
  for (var i = 0; i < 5; i++) {
    services[i] = timetable.Services[i]
  }
  yield put(actions.fetchTimetable.success({timetable: services, receivedAt: Date.now()}))
}

export function* getBusTimetableSaga() {
  yield takeLatest(actions.fetchTimetable.request,
    getBusTimetable,
  )}



