import React, { Component } from 'react';
import { connect } from 'react-redux';
import { addName } from '../actions'
import moment from 'moment';
import '../styles/Greeting.css';

class Greeting extends Component {

  greeting = () => {
    const time = parseInt(moment().format('HH'))
    if (time < '12') {
      return 'Good morning,'
    } else if (time > '12' && time < '17') {
      return 'Good afternoon,'
    } else {
      return 'Good evening,'
    }
  }

  render() {
    console.log(this.props)
    return (
      <div className='greeting'>
        <div className='mainGreeting'>
          {this.greeting()} {this.props.name}!
        </div>
        <div className='subGreeting'>
          {this.props.weatherSummary}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    name: state.induction.name,
    weatherSummary: state.weather.weather.daily.summary
  }
}

const GreetingContainer = connect(
  mapStateToProps,
)(Greeting)

export default GreetingContainer;



