import { handleActions } from 'redux-actions';

import * as actions from '../actions';

export const initialState = {
  isLoading: true,
  weather: {},
  receivedAt: '',
  url: 'https://cors-anywhere.herokuapp.com/https://api.darksky.net/forecast/664e5ccc894fb00d19e881ce36b9b30b/-41.2888,174.7772?units=ca'  
};

const initialActions = {
  [actions.fetchWeather.request]: (state, action) => ({
    ...state,
    isLoading: true,
  }),

  [actions.fetchWeather.success]: (state, action) => ({
    ...state,
    isLoading: false,
    weather: action.payload.weather,
    receivedAt: action.payload.receivedAt
  }),
};

export default handleActions(initialActions, initialState);


