import React, { Component } from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import ReactAnimatedWeather from 'react-animated-weather'

import * as actions from '../actions'

import '../styles/Weather.css'

const defaults = {
  icon: 'CLEAR_DAY',
  color: 'white',
  size: 50,
  animate: true
}

class Weather extends Component {

    componentDidMount = () => {
        this.props.fetchWeather.request()
        setInterval(this.props.fetchWeather.request, 120000)
    }

    iconTransform = () => {
        //clear-day to CLEAR_DAY
        const upperCaseIcon = this.props.weatherData.weather.currently.icon.toUpperCase()
        const transformedIcon = upperCaseIcon.replace('-', '_')
        const secondtransformedIcon = transformedIcon.replace('-', '_')
        console.log(secondtransformedIcon)
        return secondtransformedIcon
    }

    reduceDecimal = (string) => {
        return parseInt(string)
    }

    render() {
    return (
        <div className='weatherWrapper'>
            {this.props.weatherData.isLoading ?
            null
            :
            <div className='weather'>
                <div className='weatherData'>
                    <div>{this.reduceDecimal(this.props.weatherData.weather.currently.temperature)} °C</div>
                    <div>{this.reduceDecimal(this.props.weatherData.weather.currently.windSpeed)} km/h</div>
                </div>
                <div className='weatherIcon'>
                    <div>
                        <ReactAnimatedWeather
                            icon={this.iconTransform()}
                            color={defaults.color}
                            size={defaults.size}
                            animate={defaults.animate}
                        />
                    </div>
                </div>
            </div>
            }
        </div>
    )
    }
}

const mapStateToProps = state => {
    return {
      weatherData: state.weather
    }
  }
  
const mapDispatchToProps = dispatch => ({
    fetchWeather: bindActionCreators(actions.fetchWeather, dispatch)    
})

const WeatherContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(Weather)

export default WeatherContainer;



